.feedLinkBox {
  width: 100%;
  height: 100%;
  font-weight: 600;
  cursor: pointer;
  position: relative;
}

.feedLinkBox__NavLink {
  text-decoration: none;
  color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 5% 20% 5% 10%;
  height: 100%;
  width: 100%;
  -o-text-overflow: clip;
     text-overflow: clip;
  overflow: hidden;
  overflow-wrap: break-word;
  word-break: break-all;
}

.feedLinkBox__NavLink--active {
  color: red;
  text-shadow: 0px 0px 1em;
}

.feedLinkBox__removeLinkButton {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 20%;
  right: 0;
  top: 2.5em;
}



