* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
} 


body {
  font-family: sans-serif;
}

@media (max-width: 768px) {
  body {
    font-size: 70%;
  }

  input::-webkit-input-placeholder {
    font-size: 70%;
  }

  input:-ms-input-placeholder {
    font-size: 70%;
  }

  input::-ms-input-placeholder {
    font-size: 70%;
  }

  input::placeholder {
    font-size: 70%;
  }
}


